	module.exports = {"data":{"markdownRemark":{"html":"<h3>Lock Reports</h3>\n<p>You can now lock reports to prevent new comments on publicly disclosed reports.\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/sep_2016_lock_report-8de802541108dedbb8d90dcaf14cd77d-552db.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 34.19972640218879%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"sep_2016_lock_report\"\n        title=\"\"\n        src=\"/static/sep_2016_lock_report-8de802541108dedbb8d90dcaf14cd77d-e7ffa.png\"\n        srcset=\"/static/sep_2016_lock_report-8de802541108dedbb8d90dcaf14cd77d-49813.png 125w,\n/static/sep_2016_lock_report-8de802541108dedbb8d90dcaf14cd77d-dc8a5.png 250w,\n/static/sep_2016_lock_report-8de802541108dedbb8d90dcaf14cd77d-e7ffa.png 500w,\n/static/sep_2016_lock_report-8de802541108dedbb8d90dcaf14cd77d-dda24.png 750w,\n/static/sep_2016_lock_report-8de802541108dedbb8d90dcaf14cd77d-c5f18.png 1000w,\n/static/sep_2016_lock_report-8de802541108dedbb8d90dcaf14cd77d-552db.png 1462w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h3>Assign Report Through API</h3>\n<p>Programs can now assign reports to team members using the API. See the API documentation for how to assign a report <a href=\"https://api.hackerone.com/docs/v1#/reports/assignee\">here</a>.</p>\n<h3>Notifications Page</h3>\n<p>We’ve created a notifications page so that you can have a clear overview of your notifications. Go to <a href=\"https://hackerone.com/notifications\">https://hackerone.com/notifications</a> to see your notifications.</p>\n<h3>Filter Inbox by Program</h3>\n<p>Hackers can now filter reports in their inbox by program using the <b>Reported to</b> field so that they don't have to filter through reports with their own eyes.\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/sep_2016_filter_inbox-d9e99975f9ca5490c21685f68ad7d0bd-b5e17.jpg\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 342px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 44.44444444444444%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"sep_2016_filter_inbox\"\n        title=\"\"\n        src=\"/static/sep_2016_filter_inbox-d9e99975f9ca5490c21685f68ad7d0bd-b5e17.jpg\"\n        srcset=\"/static/sep_2016_filter_inbox-d9e99975f9ca5490c21685f68ad7d0bd-d5ec2.jpg 125w,\n/static/sep_2016_filter_inbox-d9e99975f9ca5490c21685f68ad7d0bd-0a0cf.jpg 250w,\n/static/sep_2016_filter_inbox-d9e99975f9ca5490c21685f68ad7d0bd-b5e17.jpg 342w\"\n        sizes=\"(max-width: 342px) 100vw, 342px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>","frontmatter":{"path":"/changelog/2016/september","title":"September 2016"}}},"pathContext":{}}

